<template>
    <section
        :id="sectionId"
        :class="{ 'animation-section': true, active: isActive }"
    >
        <div class="container">
            <div class="card-wrap" v-for="(card, index) in cards" :key="index" @click="handleCardClick(card, index)">
                <div
                    class="card"
                    :style="{
                        transform: `rotateY(${mousePX * 30}deg) rotateX(${
                            mousePY * -30
                        }deg)`,
                    }"
                    @mousemove="handleMouseMove(index, $event)"
                    @mouseenter="handleMouseEnter"
                    @mouseleave="handleMouseLeave"
                    :ref="'card-' + index"
                >
                    <div
                        class="card-bg"
                        :style="{
                            'background-image': `url(${card.image})`,
                            transform: `translateX(${
                                mousePX * -40
                            }px) translateY(${mousePY * -40}px)`,
                        }"
                    ></div>
                    <div class="card-info">
                        <h1>{{ card.header }}</h1>
                        <p>{{ card.content }}</p>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import { ref, onMounted } from "vue";

export default {
    props: {
        isActive: Boolean,
        cards: {
            type: Array,
            required: true
        }
    },
    methods: {
    handleCardClick(card) {
      if (card.header == "Games") {
        this.$router.push("/games"); 
      }
      if (card.header == "Applications") {
        this.$router.push("/applications"); 
      }
      if (card.header == "Integrations") {
        this.$router.push("/integrations"); 
      }
      if (card.header == "Animations") {
        this.$router.push("/animations"); 
      }
    },
  },
    setup() {
        const mouseX = ref(0);
        const mouseY = ref(0);

        let mouseLeaveDelay = null;
        let cardRefs = [];

        const handleMouseMove = (index, e) => {
            mouseX.value = e.pageX;
            mouseY.value = e.pageY;

            const cardRef = cardRefs[index];
            const rect = cardRef.getBoundingClientRect();
            const cardX = rect.left + rect.width / 2;
            const cardY = rect.top + rect.height / 2;
            const offsetX = mouseX.value - cardX;
            const offsetY = mouseY.value - cardY;
            const mousePX = offsetX / rect.width; // Définition de mousePX
            const mousePY = offsetY / rect.height; // Définition de mousePY

            cardRef.style.transform = `rotateY(${mousePX * 30}deg) rotateX(${
                mousePY * -30
            }deg)`;
        };

        const handleMouseEnter = () => {
            clearTimeout(mouseLeaveDelay);
        };

        const handleMouseLeave = () => {
            mouseLeaveDelay = setTimeout(() => {
                mouseX.value = 0;
                mouseY.value = 0;
            }, 1000);
        };

        onMounted(() => {
            cardRefs = Array.from(document.querySelectorAll(".card"));
        });

        return {
            handleMouseMove,
            handleMouseEnter,
            handleMouseLeave,
        };
    },
};
</script>

<style>




.container {
    padding: 0 80px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    height: 100vh;
}

.card-wrap {
    margin: 10px;
    transform-style: preserve-3d;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}

.card-wrap:hover .card-info {
    transform: translateY(0);
}

.card-wrap:hover .card-info p {
    opacity: 1;
}

.card-wrap:hover .card-info,
.card-wrap:hover .card-info p {
    transition: 0.6s cubic-bezier(0.23, 1, 0.32, 1);
}

.card-wrap:hover .card-info:after {
    transition: 5s cubic-bezier(0.23, 1, 0.32, 1);
    opacity: 1;
    transform: translateY(0);
}

.card-wrap:hover .card-bg {
    transition: 0.6s cubic-bezier(0.23, 1, 0.32, 1),
        opacity 5s cubic-bezier(0.23, 1, 0.32, 1);
    opacity: 0.8;
}

.card-wrap:hover .card {
    transition: 0.6s cubic-bezier(0.23, 1, 0.32, 1),
        box-shadow 2s cubic-bezier(0.23, 1, 0.32, 1);
    box-shadow: rgba(255, 255, 255, 0.2) 0 0 40px 5px,
        rgba(255, 255, 255, 1) 0 0 0 1px, rgba(0, 0, 0, 0.66) 0 30px 60px 0,
        inset #333 0 0 0 5px, inset white 0 0 0 6px;
}

.card {
    position: relative;
    flex: 0 0 240px;
    width: 240px;
    height: 320px;
    background-color: #333;
    overflow: hidden;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.66) 0 30px 60px 0, inset #333 0 0 0 5px,
        inset rgba(255, 255, 255, 0.5) 0 0 0 6px;
    transition: 1s cubic-bezier(0.445, 0.05, 0.55, 0.95);
}

.card-bg {
    opacity: 0.5;
    position: absolute;
    top: -20px;
    left: -20px;
    width: 100%;
    height: 100%;
    padding: 20px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    transition: 1s cubic-bezier(0.445, 0.05, 0.55, 0.95),
        opacity 5s 1s cubic-bezier(0.445, 0.05, 0.55, 0.95);
    pointer-events: none;
}

.card-info {
    padding: 20px;
    position: absolute;
    bottom: 0;
    color: #fff;
    transform: translateY(40%);
    transition: 0.6s 1.6s cubic-bezier(0.215, 0.61, 0.355, 1);
}

.card-info p {
    opacity: 0;
    text-shadow: rgba(0, 0, 0, 1) 0 2px 3px;
    transition: 0.6s 1.6s cubic-bezier(0.215, 0.61, 0.355, 1);
}

.card-info > * {
    position: relative;
    z-index: 1;
}

.card-info:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    width: 100%;
    height: 100%;
    background-image: linear-gradient(
        to bottom,
        transparent 0%,
        rgba(0, 0, 0, 0.6) 100%
    );
    background-blend-mode: overlay;
    opacity: 0;
    transform: translateY(100%);
    transition: 5s 1s cubic-bezier(0.445, 0.05, 0.55, 0.95);
}

.card-info h1 {
    font-family: "Playfair Display";
    font-size: 36px;
    font-weight: 700;
    text-shadow: rgba(0, 0, 0, 0.5) 0 10px 10px;
}
</style>
